import { restaurentList } from "../utils/mockData";
import RestaurantCard from "./RestaurentCard";
import FilterwithIconsbar from "./FilterwithIconsbar";

const BodyContainer = () => {
  return (<div className="body-container">
    <div className="filter-with-icons">
      <section>
      <FilterwithIconsbar></FilterwithIconsbar>
      </section>
    </div>
    <div className="restaurent-card">
    {restaurentList.map((restaurant) => <RestaurantCard key={restaurant.info.id}info={restaurant.info}></RestaurantCard>)}
    </div>
  </div>);
};

export default BodyContainer;
